// desktop menu items
.menu-item-d {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  width: calc(100% - 40px);
  height: 50px;
  margin-right: 10px;
  margin-bottom: 0;
  padding-left: 10px;
  background-color: transparent;

  .menu-item-icon {
    display: flex;
    align-items: center;
    width: 20px;
  }

  .menu-item-icon-default {
    display: flex;
    justify-content: flex-end;
  }

  .menu-item-icon-active {
    display: none;
  }

  // Border for all except the last one
  &:not(:last-of-type) {
    border-bottom: 1px solid transparentize(#fff, .65);
  }

  // active menu item state
  &:hover, &.is-active {
    position: relative;
    margin-left: -10px;
    border-bottom: none;

    &::before {
      display: block;
      content: '';
      position: absolute;
      top: 0;
      right: -14px;
      width: calc(100% + 44px);
      height: 100%;
      border-left: 5px solid #d8cdba;
      box-shadow: 0 1.5px 7.5px 0 rgba(0, 0, 0, 0.16);
      background-color: #ffffff;
      border-top-right-radius: 2.5px;
      border-top-left-radius: 2.5px;
    }

    .menu-item-name {
      position: relative;
      z-index: 1;
      color: #585077;
    }

    .menu-item-icon-default {
      display: none;
    }

    .menu-item-icon-active {
      display: flex;
      transform: translate(0, 0);
    }
  }

  @media(min-width: 992px) {
    cursor: pointer;
  }
}

.menu-item-name {
  line-height: 1.125;
  margin-left: 15px;
  font-size: 1.125rem;
  color: #fff;
}

