.sub-menu-list {}

.sub-menu-items {
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 40px;
}

.sub-menu-item {
  display: block;
  line-height: 1.25;
  color: #585077;
  margin: 0 15px;
  padding: 10px 0 6px;
  white-space: nowrap;
  cursor: pointer;

  &:hover {
    position: relative;

    &::after {
      position: absolute;
      bottom: -6px;
      left: 50%;
      transform: translate(-50%, 0);
      width: 120%;
      height: 4px;
      display: block;
      content: '';
      background-color: #585077;
    }
  }

  &.is-active {
    position: relative;

    &::after {
      position: absolute;
      bottom: -6px;
      left: 50%;
      transform: translate(-50%, 0);
      width: 120%;
      height: 4px;
      display: block;
      content: '';
      background-color: #585077;
    }
  }

  &.more-btn {
    &:hover {
      &::after {
        display: none;
      }
    }

    &.is-active {
      &::after {
        display: none;
      }
    }
  }

  &:first-of-type {
    margin: 0 15px 0 0;
  }

  &:last-of-type {
    //margin: 0 0 0 15px;
  }
}

.sub-menu-container-inner {
  border-bottom: 2px solid #d7d7d7;
  margin-right: 15px;
  margin-left: 15px;
  background-color: #fff;

  .row {
