// More btn navigation
.more-btn {
  .more-btn-inner {
    display: flex;
    flex-direction: row;
    align-items: center;
  }

  i {
    //font-size: 1.25rem;
    margin-right: 8px;
    -webkit-transition: all .2s ease;
    transition: all .2s ease;
    color: #585077;
    font-weight: 300;
  }

  .sub-menu-container {
    display: none;
  }

  &:hover {
    .sub-menu-container {
      display: block;
      padding-top: 34px;
      width: 200px;
      height: 200px;
      position: absolute;
      bottom: 34px;
      left: 0;
      transform: translate(0, 100%);
      z-index: 10;
    }

    .hide-sub-menu {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: stretch;
      padding-inline-start: 0;
      border-radius: 5px;
      background-color: #ffffff; //
      box-shadow: 0 1.5px 25px 0 rgba(88, 80, 119, 0.19);
    }

    .hide-sub-menu > li {
      margin: 2px 0;
      padding: 10px 15px;
      list-style: none;
      font-size: 1rem;
      color: #585077;

      &:hover {
        color: #16181b;
        text-decoration: none;
        background-color: #f8f9fa;
      }
    }

    .hide-sub-menu > li > a {
      display: flex;
      flex-direction: row;
      align-items: center;
    }

    .hide-sub-menu > li > a i {
      font-size: 1.5rem;
      margin-right: 8px;
      text-align: center;
      -webkit-transition: all .2s ease;
      transition: all .2s ease;
      color: #585077;
      font-weight: 300;
    }
  }
}

