// Dropdown
//.dropdown-info-wrapper,
.dropdown-list-wrapper {
  &.active {
    position: relative;

    &:before {
      position: absolute;
      top: -10px;
      left: -28px;
      transform: translate(0, 0);
      display: block;
      content: '';
      width: .1px;
      height: .1px;
      border-bottom: 10px solid #585077;
      border-top: none;
      border-right: 6px solid transparent;
      border-left: 6px solid transparent;
    }
  }
}

.dropdown-info,
.dropdown-list {
  &.visible {
    z-index: 9001;
    position: absolute;
    left: 50%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;

    border-radius: 5px;
    box-shadow: 0 1.5px 15px 0 rgba(0, 0, 0, 0.16);
    //box-shadow: none !important;
    //border: 1px solid #585077;
    background-color: #ffffff;

    .dropdown-items-wrapper {
      padding: 10px;
      height: auto;
      min-height: 300px;
      width: 300px;
      overflow-x: hidden;
      overflow-y: auto;
    }

    .dropdown-header {
      position: relative;
      display: flex;
      align-items: center;
      height: 50px;
      padding-bottom: 5px;
      background-color: #585077;
      color: #fff;
      font-family: "Raleway", sans-serif;
      font-size: 10pt;

      &:after {
        position: absolute;
        bottom: 0;
        left: 0;
        display: block;
        content: '';
        width: 100%;
        height: 6px;
        background-color: #fff;
        border-top-left-radius: 10px;
        border-top-right-radius: 10px;
        //margin-top: -7px;
      }
    }
  }

  &.invisible {
    display: none;
  }
}

.dropdown-list {
  &.visible {
    bottom: 0;
    //transform: translate(-50%, 100%);
    transform: translate(-60%, 100%);
    max-height: 465px;
  }
}

.dropdown-info {
