// User info
.user-outer {
  height: auto;
  margin-bottom: 0;
  user-select: none;
  background: #fff;


  &.show {
    border-bottom: none;
    background: #fff;
    border-radius: 5px 5px 0 0;
    //box-shadow: 0 0 4px 2px #ccc;

    .user {
      &.is-active {
        border-bottom-color: transparent;
        background: #585077;
        border-radius: 5px 5px 0 0;
        box-shadow: 0 3px 4px 2px #f2f4f6;
        height: auto;
        padding: 1.25rem .75rem .375rem;

        .info-inner {
          color: #fff;

          &::after {
            border-right: 1px solid #fff;
            border-bottom: 1px solid #fff;
          }
        }

        .image-container {
          &::before {
            display: none;
          }
        }
      }
    }
  }
}

.user-outer .user {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  width: 180px;
  height: 100%;

  &:focus {
    outline: none;
  }
}

.info {
  display: flex;
  align-items: flex-end;
  height: 100%;
  //margin-left: 10px;
}

.info-inner {
  position: relative;
  display: flex;
  flex-direction: column;
  line-height: 1.25;
  padding-right: 20px;
  color: #585077;
  cursor: pointer;

  &::after {
    position: absolute;
    right: 0.5em;
    bottom: .4em;
    top: auto;
    content: "";
    display: block;
    border-top: 1px solid transparent;
    border-right: 1px solid #342e4a;
    border-bottom: 1px solid #342e4a;
    border-left: 1px solid transparent;
    width: 5px;
    height: 5px;
    transform: rotate(45deg)
  }
}

