.image-wrapper {
  .image-container {
    position: relative;
    display: flex;
    align-items: flex-end;
    width: 50px;
    height: 60px;
    padding: 10px 4px 0px;
    //width: 60px;
    //height: 60px;
    //padding: 2px 4px;

    &::before {
      display: block;
      content: '';
      position: absolute;
      top: 0;
      left: 50%;
      width: 100%;
      height: 140%;
      //padding: 3px;
      border-bottom-left-radius: 30px;
      border-bottom-right-radius: 30px;
      background-color: #585077;
      z-index: 1;
      transform: translate(-50%, -25%);
    }
  }

  img {
    z-index: 2;
    position: relative;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    object-fit: cover;
  }
}

